html, body {
  background: url('https://images.unsplash.com/photo-1623134915831-9dcb66cf9a8e?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2671&q=80') center center no-repeat;
  background-size: cover;
  color: white;
  height: 100%;
  width: 100%;
}

.content {
  margin: auto;
  margin-top: 20px;
  width: 100%;
  height: auto;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
}

.title {
  padding-top: 50px;
  text-align: center;
  font-weight: 600;
  font-size: 3em;
  letter-spacing: 0.15rem;
  color: #fff;
}

label {
  color: #333;
}

.label-head {
  font-weight: 600;
  font-size: 20px;
}

.label-head-small {
  font-weight: 600;
  font-size: 16px;
}

.result-container {
  text-align: center;
  font-weight: 600;
  font-size: 4em;
  letter-spacing: 0.15rem;
  padding: 5px 0 10px;
}

#result {
  color: #000;
}

.col25.form-group.col {
  -ms-flex: 0 0 20%;
  flex: 0 0 20%;
  max-width: 20%;
}
